import blog from '@/api/blog'

//因为用户已经登录，可以从Vux getters获取 我的信息
import { mapGetters} from 'vuex'

export default {
    data() {
        return {
            index:'',
            blogs: [],
            page: 1,
            total:0
        }
    },

    computed: {  //计算 
        ...mapGetters(['user'])
    },

    created() {
        //判断this.$route.query.page是否存在
        this.page = parseInt(this.$route.query.page) || 1 //parseInt() 函数可解析一个字符串，并返回一个整数。

        blog.getBlogsByUserId(this.user.id, { page: this.page }) //this.user.id是通过getters获取到的
            .then(res => {
                // console.log(res)
                this.blogs = res.data
                this.page = res.page
                this.total = res.total
            })
        //精确：获取指定用户的博客
        /*
        getBlogsByUserId(userId, { page=1, atIndex } = { page: 1}) {
          return this.getBlogs({ userId, page, atIndex })
        }
        */ 
    },

    methods: {
        onPageChange(newPage) {
         blog.getBlogsByUserId(this.user.id,{page:newPage}) //精确：获取指定页数博客列表，只得到显示在首页的博客
            .then(res => {
                this.blogs = res.data  //数据存放到blog
                this.total = res.total //数据替换total
                this.page = res.page   //数据替换page
                this.$router.push( {path:"/my",query:{ page:newPage } } )
            })

        },
        
        splitDate(dataStr) {
            let dateObj = typeof dataStr === 'object' ? dataStr : new Date(dataStr)
            return {
                date: dateObj.getDate(),
                month: dateObj.getMonth() + 1,
                year: dateObj.getFullYear()
            }
        },

        //删除博客
        /*promise
        nDelete(blogId) { 
            console.log(blogId)
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                return blog.deleteBlog({ blogId })
            }).then(res => {
                        this.$message({ message: '删除成功!'})
            }).catch(() => {
                this.$message({message: '已取消删除'});          
            });
        }
        */
        
        //删除博客
        // async/await
        async onDelete(blogId) { //删除博客
            await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            })
            await blog.deleteBlog({ blogId })
            this.$message({ type: 'success',message: '删除成功!' })

            //点击删除博客不会立即消失，移除了对象，使其不在页面中显示，还需要删除DOM树中的节点，通过filter过滤
            this.blogs = this.blogs.filter(blog =>
                blog.id != blogId)//删除后立即重新过滤（刷新）整个blog,把不存在的bolg.id博客清除掉
            
        }
      

        //删除博客 id 为:blogId 的博客
        /*
        deleteBlog({ blogId }) {
             return request(URL.DELETE.replace(':blogId', blogId), 'DELETE')
        }
        */
        
        }   
}


